body {
  margin: 0;
  font-family: 'Nunito','Comforter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.SvgIcon {
 -webkit-transform: scale(2.5);
         transform: scale(2.5);
 color:wheat!important;
 
}



 .ButtonBase {
  background-color: orange;
 }
 

 .MuiBox-root {
  justify-content: right;
 }







/* =================== FORM =================== */

form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
 
 
}

input,
textarea {  
  width: 30%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: floralwhite;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: black;
  font-size: 15px;   
  font-family: "tahoma";
}


.btn-primary {
  width: 100px;
  font-weight: bold;
  color: "rgb(47, 79, 79)";
  background-color: wheat;
  font-size: 15px;   
  font-family: "tahoma";
  border: 2px solid var(--color-primary-variant);
  border-radius: 0.5rem;
  
}
@import-normalize;

:root {
  --darkColor: rgb(32, 33, 36);
  --lightColor: wheat;
  --primeColor: rgba(150, 202, 27, 0.911);
  --secondColor: rgba(223, 100, 78, 0.911);
  --secondColor: rgba(207, 126, 120, 0.911);
  --primeBorder: dashed 2px var(--lightColor);
  --secondBorder: solid 1px var(--lightColor);
  --borderRadius: 1rem;
}

::selection {
  background-color: rgb(221, 53, 53);
  color: rgb(32, 33, 36);
  color: var(--darkColor);
}

:target {
  padding-top: 8rem;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  transition: all 0.5s ease-in-out;
}

body {
  box-sizing: border-box;
  font-size: 3rem;
  line-height: 1.5;
  font-family: 'Nunito', 'Outfit', sans-serif;
  background-color: rgb(32, 33, 36);
  background-color: var(--darkColor);
  text-align: center;
  color: wheat;
  color: var(--lightColor);
}


h1 {
  font-size: 4.5rem;
  text-transform: uppercase;
  color: wheat;
  color: var(--lightColor);
  padding: 5rem 3rem 2rem 3rem;

}

h2 {
  font-size: 4rem;
  color: rgba(150, 202, 27, 0.911);
  color: var(--primeColor);
  font-family: 'Comforter', cursive, sans-serif;
}

hr {
  border: solid 1px wheat;
  border: var(--secondBorder);
  width: 80%;
}

.primary,
.contact-form {
  color: wheat;
  color: var(--lightColor);
  margin: "1 rem"
}

h3 {
  display: block;
  font-size: 3rem;
  color: rgba(150, 202, 27, 0.911);
  color: var(--primeColor);

}

header {
  width: 100%;
  margin: 0 auto;
}

main {
  margin: 5rem;
}

svg:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  transition: all 0.5s ease-in-out;
}



.project:hover,
button:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition: all 1s ease-in-out;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
